<template>
  <div>
    <p>您的功德：<span>{{ count }}</span></p>
    <div @click="clickMuYu">
      <img id="wf" src="@/assets/img/imgs/muyubai.png">
    </div>
    <audio id="audio" src="@/assets/mp3/muyu.mp3"></audio>
  </div>
</template>

<script>
export default {
  name: "muyu",

  data() {
    return {
      count: 0
    }
  },

  mounted() {
    document.body.style.background = '#000000'
    this.$store.state.navigation_bar_time_color = '#FFFFFF'
  },

  methods: {
    clickMuYu() {
      let music = document.getElementById('audio')
      let wf = document.getElementById('wf')

      music.play()

      let body = document.body
      let span = document.createElement('span')

      span.style.position = 'absolute'
      span.style.left = wf.offsetLeft + 'px'
      span.style.top = wf.offsetTop - 150 + 'px'
      span.innerHTML = '功德+1'
      span.style.transform = 'scale(1.2)'
      span.style.opacity = 0
      body.appendChild(span)
      this.count++

      setTimeout(() => {
        body.removeChild(span)
      }, 1500)

      wf.style.animation = 'zha .1s linear'
      setTimeout(() => {
        wf.style.animation = ''
      }, 100)
    },
  },

  destroyed() {
    document.body.style.background = '#FFFFFF'
    this.$store.state.navigation_bar_time_color = '#000000'
  },
}
</script>

<style scoped>
body {
  position: relative;
  background-color: #000;
}

#wf {
  position: absolute;
  left: 45%;
  top: 45%;
  width: 215px;
}

@keyframes qiao {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(-30deg);
  }

  100% {
    transform: rotate(0);
  }
}

@keyframes zha {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

span {
  position: absolute;
  color: #fff;
  transition: 1.5s;
}

p {
  color: #fff;
}
</style>
